"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Navbar from "../../../components/Navbar";

interface SupplyItem {
  id: number;
  name: string;
  category: string;
  quantity: number;
  minQuantity: number;
  lastChecked: string;
  status: "In Stock" | "Low Stock" | "Out of Stock";
}

export default function MedicalInventoryPage() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);
  const [mounted, setMounted] = useState(false);
  const [supplies, setSupplies] = useState<SupplyItem[]>([
    {
      id: 1,
      name: "Sterile Bandages (Pack)",
      category: "Wound Care",
      quantity: 45,
      minQuantity: 10,
      lastChecked: "2023-10-25",
      status: "In Stock",
    },
    {
      id: 2,
      name: "Alcohol Swabs",
      category: "Disinfectants",
      quantity: 120,
      minQuantity: 50,
      lastChecked: "2023-10-26",
      status: "In Stock",
    },
    {
      id: 3,
      name: "Nitrile Gloves (Box)",
      category: "PPE",
      quantity: 8,
      minQuantity: 20,
      lastChecked: "2023-10-24",
      status: "Low Stock",
    },
    {
      id: 4,
      name: "Syringes (10ml)",
      category: "Equipment",
      quantity: 0,
      minQuantity: 30,
      lastChecked: "2023-10-20",
      status: "Out of Stock",
    },
    {
      id: 5,
      name: "Ibuprofen (200mg)",
      category: "Medication",
      quantity: 85,
      minQuantity: 50,
      lastChecked: "2023-10-26",
      status: "In Stock",
    },
    {
      id: 6,
      name: "Oxygen Masks",
      category: "Equipment",
      quantity: 12,
      minQuantity: 15,
      lastChecked: "2023-10-22",
      status: "Low Stock",
    },
  ]);

  // Initial setup para pag-load ng page
  useEffect(() => {
    setMounted(true);
    // Check natin kung may user na naka-login sa storage
    const storedUser = localStorage.getItem("user");
    if (storedUser) {
      setUser(JSON.parse(storedUser));
    } else {
      // Pag walang user, redirect muna natin sa login
      router.push("/login");
    }
  }, [router]);

  // Dito natin ise-set 'yung kulay ng bawat status tag
  const getStatusColor = (status: string) => {
    switch (status) {
      case "In Stock":
        return "bg-green-100 text-green-800 border-green-200";
      case "Low Stock":
        return "bg-yellow-100 text-yellow-800 border-yellow-200";
      case "Out of Stock":
        return "bg-red-100 text-red-800 border-red-200";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  // Function para i-update 'yung quantity at status ng bawat gamit
  const handleUpdateQuantity = (id: number, newQuantity: number) => {
    setSupplies((prev) =>
      prev.map((item) => {
        if (item.id === id) {
          let newStatus: SupplyItem["status"] = "In Stock";
          // Automatic na nating papalitan 'yung status base sa stock count
          if (newQuantity === 0) newStatus = "Out of Stock";
          else if (newQuantity <= item.minQuantity) newStatus = "Low Stock";

          return {
            ...item,
            quantity: newQuantity,
            status: newStatus,
            lastChecked: new Date().toISOString().split("T")[0], // Kunin natin 'yung date ngayong araw
          };
        }
        return item;
      }),
    );
  };

  if (!mounted || !user) return <div className="min-h-screen bg-gray-50" />;

  return (
    <div className="min-h-screen bg-gray-50 font-sans">
      <Navbar user={user} />

      <main className="max-w-7xl mx-auto py-12 px-4 sm:px-6 lg:px-8">
        <div className="mb-8 flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
          <div>
            <h1 className="text-3xl font-extrabold text-gray-900 tracking-tight">
              Medical Supply Inventory
            </h1>
            <p className="mt-2 text-sm text-gray-500">
              Manage and track medical department supplies and stock levels.
            </p>
          </div>
          <button className="inline-flex items-center px-4 py-2 border border-transparent rounded-xl shadow-sm text-sm font-bold text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition-all">
            + Add New Item
          </button>
        </div>

        {/* Stats Overview */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <div className="bg-white p-6 rounded-2xl shadow-sm border border-gray-100">
            <div className="flex items-center">
              <div className="p-3 rounded-full bg-red-100 text-red-600">
                <svg
                  className="w-6 h-6"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
                  />
                </svg>
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">
                  Critical Low Stock
                </p>
                <p className="text-2xl font-bold text-gray-900">
                  {supplies.filter((s) => s.status === "Out of Stock").length}{" "}
                  Items
                </p>
              </div>
            </div>
          </div>

          <div className="bg-white p-6 rounded-2xl shadow-sm border border-gray-100">
            <div className="flex items-center">
              <div className="p-3 rounded-full bg-yellow-100 text-yellow-600">
                <svg
                  className="w-6 h-6"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                  />
                </svg>
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">
                  Needs Reorder
                </p>
                <p className="text-2xl font-bold text-gray-900">
                  {supplies.filter((s) => s.status === "Low Stock").length}{" "}
                  Items
                </p>
              </div>
            </div>
          </div>

          <div className="bg-white p-6 rounded-2xl shadow-sm border border-gray-100">
            <div className="flex items-center">
              <div className="p-3 rounded-full bg-green-100 text-green-600">
                <svg
                  className="w-6 h-6"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
                  />
                </svg>
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">Total Items</p>
                <p className="text-2xl font-bold text-gray-900">
                  {supplies.length}
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* Inventory List */}
        <div className="bg-white shadow-xl rounded-2xl overflow-hidden border border-gray-100">
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th
                    scope="col"
                    className="px-6 py-4 text-left text-xs font-bold text-gray-500 uppercase tracking-wider"
                  >
                    Item Name
                  </th>
                  <th
                    scope="col"
                    className="px-6 py-4 text-left text-xs font-bold text-gray-500 uppercase tracking-wider"
                  >
                    Category
                  </th>
                  <th
                    scope="col"
                    className="px-6 py-4 text-left text-xs font-bold text-gray-500 uppercase tracking-wider"
                  >
                    Stock Level
                  </th>
                  <th
                    scope="col"
                    className="px-6 py-4 text-left text-xs font-bold text-gray-500 uppercase tracking-wider"
                  >
                    Status
                  </th>
                  <th
                    scope="col"
                    className="px-6 py-4 text-left text-xs font-bold text-gray-500 uppercase tracking-wider"
                  >
                    Last Checked
                  </th>
                  <th
                    scope="col"
                    className="px-6 py-4 text-right text-xs font-bold text-gray-500 uppercase tracking-wider"
                  >
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {supplies.map((item) => (
                  <tr
                    key={item.id}
                    className="hover:bg-gray-50 transition-colors"
                  >
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-bold text-gray-900">
                        {item.name}
                      </div>
                      <div className="text-xs text-gray-500">
                        ID: #{item.id.toString().padStart(4, "0")}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-50 text-blue-700">
                        {item.category}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center space-x-3">
                        <button
                          onClick={() =>
                            handleUpdateQuantity(
                              item.id,
                              Math.max(0, item.quantity - 1),
                            )
                          }
                          className="w-6 h-6 rounded-full bg-gray-100 text-gray-600 hover:bg-gray-200 flex items-center justify-center font-bold"
                        >
                          -
                        </button>
                        <span className="text-sm font-bold text-gray-900 w-8 text-center">
                          {item.quantity}
                        </span>
                        <button
                          onClick={() =>
                            handleUpdateQuantity(item.id, item.quantity + 1)
                          }
                          className="w-6 h-6 rounded-full bg-gray-100 text-gray-600 hover:bg-gray-200 flex items-center justify-center font-bold"
                        >
                          +
                        </button>
                      </div>
                      <div className="text-xs text-gray-400 mt-1 ml-1">
                        Min: {item.minQuantity}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span
                        className={`px-3 py-1 inline-flex text-xs leading-5 font-semibold rounded-full border ${getStatusColor(item.status)}`}
                      >
                        {item.status}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {item.lastChecked}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      <button className="text-blue-600 hover:text-blue-900 font-bold hover:underline">
                        Edit
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </main>
    </div>
  );
}
